import { Component, OnInit } from '@angular/core';
import { Device } from '../../models/Device';
import { IssueService} from '../../issue.service';

@Component({
  selector: 'app-devicepage',
  templateUrl: './devicepage.component.html',
  styleUrls: ['./devicepage.component.css']
})
export class DevicepageComponent implements OnInit {
  devices: Device[];
  device_selected: Device;
  add_new_device = false;
  constructor(private issueService: IssueService) { }

  ngOnInit() {
    this.getDevices();
  }
  getDevices() {
    this.issueService.handle_get_devices().subscribe((issue) => {
      return this.devices = (issue["status"] == 200) ? issue["devices"] : [];
    });
  }

  onSelect(device: Device) {
    this.device_selected = device;
  }

  handleClose($event):void {
    this.device_selected = $event;
    this.add_new_device = $event;
  }

  handle_add_device() {
    this.add_new_device = true;
  }

  handle_new_device($event) {
    this.devices.push($event);
  }
}
