import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-inforuserpage',
  templateUrl: './inforuserpage.component.html',
  styleUrls: ['./inforuserpage.component.css']
})
export class InforuserpageComponent implements OnInit {
  readonly = true
  constructor() { }

  ngOnInit() {
  }

  handle_edit() {
    this.readonly = false;
  }

  handle_update() {
    this.readonly = true;
  }

  handle_cancel() {
    this.readonly = true;
  }
}
