import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';

@Component({
  selector: 'app-alerbox',
  templateUrl: './alerbox.component.html',
  styleUrls: ['./alerbox.component.css']
})
export class AlerboxComponent implements OnInit {
  @Output() handle_event = new EventEmitter(); 
  @Input () data = {
    status : false,
    message: ''
  }
  constructor() { }

  ngOnInit() {
  }

  handle_close() {
    this.handle_event.emit({'status' : this.data.status, 'close' : false});
  }
  
}
