import { Component, OnInit, EventEmitter, Input, Output } from '@angular/core';
import { ValidationErrors} from '@angular/forms';
import { IssueService } from '../../issue.service';

@Component({
  selector: 'app-formadddevice',
  templateUrl: './formadddevice.component.html',
  styleUrls: ['./formadddevice.component.css']
})
export class FormadddeviceComponent implements OnInit {
  @Output() handle_show = new EventEmitter();
  @Output() transfer_new_device = new EventEmitter();
  @Input() errors: ValidationErrors;
  
  new_device = {
    ip: '',
    phone_number: '',
    address: '',
  }
  is_show = false;
  data = { 'status' : true, 'message' : ''}

  constructor(private issueService: IssueService) { }

  ngOnInit() {
  }

  onSelect() {
    this.handle_show.emit(false);
  }

  onCloseAlert($event) {
    this.is_show = $event.close;
    if($event.status) {
      this.handle_show.emit($event.close);
    }
  }

  handle_add_new_device() {
    this.issueService.handle_add_new_device(this.new_device).subscribe((issue) => {
      this.is_show = true;
      if(issue["status"] == 201) {
        this.data = {'status' : true, 'message' : 'Add new device success!!'};
        this.transfer_new_device.emit(issue['device'])
      } else {
        this.data = {'status' : false, 'message' : 'Add new device fail!!'};
      }
    });
  }
}
