import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { ValidationErrors } from '@angular/forms';
import { IssueService } from '../../issue.service';

@Component({
  selector: 'app-formadduser',
  templateUrl: './formadduser.component.html',
  styleUrls: ['./formadduser.component.css']
})
export class FormadduserComponent implements OnInit {
  @Output() handle_show = new EventEmitter();
  @Output() transfer_new_user = new EventEmitter();
  @Input() errors: ValidationErrors;

  newuser = {
    full_name: '',
    user_name: '',
    email: '',
    password: '',
    confirm_password: ''
  }
  is_show = false;
  data = {
    'status': true,
    'message': ''
  }
  constructor(private issueService: IssueService) { }

  ngOnInit() {
  }

  onSelect() {
    this.handle_show.emit(false);
  }

  onCloseAlert($event) {
    this.is_show = $event.close;
    if($event.status) {
      this.handle_show.emit($event.close);
    }
  }
  
  handle_add_new_user() {
    this.issueService.handle_add_user(this.newuser).subscribe((issue) => {
      this.is_show = true;
      if(issue["status"] == 201) {
        this.data = {'message' : 'Add new user success!!', 'status' : true};
        this.transfer_new_user.emit(issue["data"]);
      } else {
        this.data = {'message' : 'Add new user fail!!', 'status' : false};
      }
    });
  }
}
