import { Component, OnInit } from '@angular/core';
import { Device } from 'src/app/models/Device';
import { Router } from '@angular/router';
import { IssueService } from '../../issue.service';
import { CookieService } from 'ngx-cookie-service';

@Component({
  selector: 'app-homepage',
  templateUrl: './homepage.component.html',
  styleUrls: ['./homepage.component.css']
})
export class HomepageComponent implements OnInit {
  token = '';
  devices: Device[];
  device_selected: Device;
  constructor(private issueService: IssueService, private cookieService: CookieService, private router: Router) { }

  ngOnInit() {
    this.token = this.cookieService.get("access_token");
    if(this.token.length == 0) {
      return this.router.navigate(['/login']);
    }
    this.getDevices();
  }
  getDevices() {
    this.issueService.handle_get_devices().subscribe((issue) => {
      this.devices = issue["devices"];
    });
  }

  onSelect(device: Device) {
    this.device_selected = device;
  }

  handleClose($event):void {
    this.device_selected = $event;
  }
}
