import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Device } from '../../models/Device';
import { ValidationErrors } from '@angular/forms';
import { IssueService } from '../../issue.service';

@Component({
  selector: 'app-detail-device',
  templateUrl: './detail-device.component.html',
  styleUrls: ['./detail-device.component.css']
})
export class DetailDeviceComponent implements OnInit {
  @Input() device: Device;
  @Input() errors: ValidationErrors; 
  @Output() handle_show = new EventEmitter();
  readonly = true;
  constructor(private issueService: IssueService) { }

  ngOnInit() {
  }

  onSelect() {
    this.device = null;
    this.handle_show.emit(null);
    this.readonly = true;
  }

  edit_info() {
    this.readonly = false;
  }

  update_info() {
    console.log('aaa');
    this.issueService.handle_update_device(this.device._id, this.device).subscribe((issue) => {
      if(issue["status"] == 200) {
        this.readonly = true;
      }
    });
    
  }
}
